#cv li:before {
  content: "";
  margin: 0 1rem 0 0;
  color: #000;
}
.page-id-344 td {
	font-size:2rem;	
}
.page-id-344 ul:not(.menu) {
	padding-left:10px;
	    list-style-position: inside;
}
.link { color: #00A3E0; } /* CSS link color */
#cv a { color: #00A3E0; }
hr.style-eight {
    overflow: visible; /* For IE */
    padding: 0;
    border: none;
    border-top: medium double #333;
    color: #333;
    text-align: center;
}
hr.style-eight:after {
    content: "";
    display: inline-block;
    position: relative;
    top: -0.9em;
    font-size: 1.5em;
    padding: 0 0.25em;
    background: white;
}
hr {
	margin:8rem auto;
}
table, tr {
	    border: 1px solid #31373c;
}
@media (min-width:1053px) and (max-width: 1303px) {
	div.cell.medium-3.small-6.right-menu h1 {
		font-size:2.5rem!important;
	}
}
@media(max-width: 1052px) {
	div.cell.medium-3.small-6.right-menu h1 {
		font-size:1.6rem!important;
	}
}

@media (max-width: 1095px) {
	nav.main-nav .left-menu .menu.menu-social li.hamburger-li {
		display: block;
	}
	nav.main-nav .left-menu .menu.menu-nav li {
		display:none;
	}
}
@media (min-width:640px) and (max-width: 902px) {
	.left-menu {
		width:50%!important;
	}
		.right-menu {
		width:50%!important;
	}
}
@media (max-width: 640px) {
	h1.nav-h1 {
		letter-spacing:1rem!important;
	}
}
tbody {
	border:1px solid #31373c!important;
}
@media only screen and (max-width: 768px) {
#cv table {
overflow: hidden;
overflow-x: scroll;
display:block;
}
}
#breadcrumbs {
    max-width: 1200px;
    margin: 30px auto 0px;
}